.CreateTodoButton {
  padding: 0;
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 100%;
  cursor: pointer;
  font-size: 55px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  z-index: 1;
  letter-spacing: 0;
  line-height:0;
  transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  transform: rotate(225deg);
}